.article-main {
  padding-bottom: 20px;
  &__title {
    text-align: center;
    font-size: 50px;
    font-variant: lining-nums;
  }
  &__info {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 40px;
    padding-right: 64px;
    margin-top: 40px;
  }
  &__date,
  &__time {
    font-size: 14px;
    line-height: 120%;
  }
  &__themes {
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    gap: 10px;
    span {
      background: #F1F1F1;
      padding: 8px 10px;
      font-size: 12px;
      line-height: 120%;
      color: #83878D;
      text-transform: uppercase;
    }
  }
  &__send {
    position: absolute;
    top: 5px;
    right: 0;
    width: 24px;
    background: transparent;
    padding: 0;
    img {
      max-width: 100%;
    }
  }
  &__img {
    margin-top: 20px;
    img {
      width: 100%;
    }
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 40px;
    p {
      font-size: 25px;
      line-height: 140%;
    }
  }
  &__text2 {
    p {
      font-size: 20px;
    }
  }
  &__item {
    margin-top: 40px;
    &--active {
      .article-main__item-btn {
        transform: rotate(0);
      }
    }
    &-show {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 16px;
      border-bottom: 1px solid #000;
      padding-bottom: 10px;
      cursor: pointer;
    }
    &-title {
      font-family: 'PlayfairDisplay';
      font-size: 33px;
      line-height: 120%;
    }
    &-btn {
      width: 14px;
      transform: rotate(-180deg);
      transition: all 0.3s;
      img {
        width: 100%;
      }
    }
    &-hidden {
      margin-top: 20px;
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 10px;
      li {
        a {
          display: flex;
          align-items: center;
          gap: 16px;
          font-size: 18px;
          line-height: 120%;
        }
        a:hover {
          color: #E22429;
          text-decoration: unset !important;
        }
        a span {
          border-bottom: 1px solid #E22429;
        }
        i {
          width: 16px;
          flex-shrink: 0;
          gap: 16px;
          img {
            width: 100%;
          }
        }
        i svg path {
          fill: currentColor;
        }
      }
    }
  }
}
.headlines {
  padding: 40px 0;
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 45px;
  }
  h3 {
    font-size: 38px;
    line-height: 120%;
  }
  h4 {
    font-size: 33px;
    line-height: 120%;
  }
  h5 {
    font-size: 28px;
    line-height: 120%;
  }
  h6 {
    font-size: 23px;
    line-height: 120%;
    font-weight: 700;
  }
}
.article-paragraph {
  padding: 20px 0;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 20px;
    p {
      font-weight: 300;
      a {
        font-weight: 400;
        text-decoration: underline;
        text-decoration-color: #E22429;
        text-underline-offset: 1px;
        &:hover {
          color: #E22429 !important;
          text-decoration: unset !important;
        }
      }
    }
  }
  &__img {
    margin-top: 20px;
    img {
      width: 100%;
    }
    span {
      display: block;
      font-size: 14px;
      line-height: 140%;
      color: #83878D;
      margin-top: 10px;
    }
  }
}
.article-info {
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    border-bottom: 1px solid #83878D;
    padding: 20px 0 60px;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    p {
      font-weight: 300;
    }
  }
  &__flex {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
  }
  .order {
    .article-info__img {
      order: -1;
    }
  }
  &__img {
    img {
      width: 100%;
    }
  }
}
.article-gallery {
  &__inner {
    border-top: 1px solid #83878D;
    padding: 60px 0 45px;
    margin-top: 4px;
  }
  &__slider {
    position: relative;
    margin-top: 20px;
  }
  &__slider-big {
    position: relative;
  }
  .mySwiper {
    margin-top: 10px;
    .swiper-slide {
      border: 1px solid transparent;
      opacity: 0.6;
    }
    .swiper-slide-thumb-active {
      border: 1px solid #000;
      opacity: 1;
    }
  }
  .swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: -58px;
  }
  .swiper-button-next {
    right: -58px;
  }
  .swiper-pagination {
    top: unset;
    bottom: -20px;
    height: 2px;
    &-progressbar {
      background: #E5E5E5;
      &-fill {
        background: #2B2A29;
      }
    }
  }
}
.article-table {
  padding: 20px 0;
  .container2 {
    padding-right: 0;
  }
  &__title {
    padding-right: 30px;
  }
  &__table {
    padding-bottom: 16px;
    margin-top: 20px;
    overflow-x: auto;
    &::-webkit-scrollbar {
      height: 2px;
    }
    &::-webkit-scrollbar-track {
      background: #E5E5E5;
    }
    &::-webkit-scrollbar-thumb {
      background: #2B2A29;
    }
  }
  table {
    width: 786px;
    border-collapse: collapse;
    border-spacing: 0;
    margin-right: 15px;
    thead {
      background: #373737;
      tr th {
        position: relative;
        text-align: start;
        font-size: 16px;
        line-height: 120%;
        font-weight: 700;
        letter-spacing: 0.5px;
        color: #fff;
        padding: 16px 0;
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          right: -1px;
          transform: translate(0, -50%);
          width: 2px;
          height: calc(100% - 2px);
          background: #373737;
          pointer-events: none;
        }
      }
      tr th:first-child {
        width: 305px;
        padding-left: 25px;
      }
      tr th:last-child {
        padding-right: 25px;
      }
    }
    tbody {
      tr:nth-child(odd) {
        background: #F8F8F8;
      }
      tr:nth-child(even) {
        background: #fff;
      }
      tr td {
        font-size: 18px;
        line-height: 120%;
        font-weight: 300;
        color: #000;
        padding: 16px 0;
      }
      tr td:first-child {
        padding-left: 25px;
      }
      tr td:last-child {
        padding-right: 25px;
      }
    }
  }
}
.article-video {
  padding: 20px 0;
  &__video {
    position: relative;
    display: block;
    margin-top: 20px;
    img {
      width: 100%;
    }
  }
  .play {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 26px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 90px;
      height: 90px;
      background: rgba(255, 255, 255, 0.5);
      border-radius: 50%;
    }
    &:hover {
      &:after {
        background: #fff;
      }
    }
    img {
      position: relative;
      z-index: 1;
    }
  }
}
.article-info2 {
  padding: 20px 0;
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
    margin-top: 20px;
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 20px;
    border: 1px solid #E5E5E5;
    padding: 20px;
    &-icon {
      width: 72px;
      height: 72px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EFEFEF;
      border-radius: 50%;
      img {
        max-width: 40px;
      }
    }
    &-text {
      p {
        font-size: 16px;
        line-height: 140%;
      }
    }
    .line {
      position: absolute;
      top: 0;
      left: 0;
      width: 4px;
      height: 100%;
      background: #2B2A29;
    }
    .red-line {
      background: #E22429;
    }
  }
  .gray-bg {
    background: #FAFAFA;
    border-color: transparent;
  }
  .attention {
    border-left: unset;
    border-right: unset;
    border-color: #2B2A29;
    &:after,
    &:before {
      content: '';
      position: absolute;
      top: -4px;
      left: 0;
      width: 100%;
      height: 1px;
      background: #2B2A29;
    }
    &:before {
      top: unset;
      bottom: -4px;
    }
  }
}
.quote {
  position: relative;
  border: 4px solid #E5E5E5;
  padding: 43px 24px 43px 120px;
  margin-top: 30px;
  &-icon {
    position: absolute;
    top: 50px;
    left: 40px;
    width: 57px;
    img {
      width: 100%;
    }
  }
  &__text {
    p {
      font-size: 18px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &__user {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-top: 10px;
    &-img {
      width: 70px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
    &-text {
      strong {
        display: block;
        font-size: 18px;
        line-height: 140%;
        font-weight: 700;
      }
      span {
        display: block;
        font-size: 14px;
        line-height: 140%;
        color: #83878D;
      }
    }
  }
}
.banner {
  color: #fff;
  padding: 15px 0;
  &2 {
    color: #2b2a29;
    .banner__inner {
      background: #D6D6D6;
      &:after {
        border-color: #2b2a29;
      }
    }
    .banner__text {
      p {
        color: rgba(0, 0, 0, 0.8);
      }
    }
  }
  &__inner {
    position: relative;
    background: #1B1B1D;
    padding: 70px 50px;
    overflow: hidden;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: calc(100% - 26px);
      height: calc(100% - 26px);
      border: 1px solid #FFFFFF;
      pointer-events: none;
    }
  }
  &__desc {
    position: relative;
    z-index: 3;
    max-width: 320px;
  }
  &__text {
    margin-top: 20px;
    p {
      font-size: 16px;
      line-height: 140%;
      color: rgba(255, 255, 255, 0.6);
    }
  }
  &__btn {
    border-radius: 1px;
    padding: 16px 32px;
    margin-top: 20px;
    span {
      font-size: 16px;
    }
  }
  &__btn2 {
    background: transparent;
    border: 1px solid #fff;
    padding: 16px 32px;
    color: #fff;
    text-transform: uppercase;
    span {
      font-size: 16px;
      line-height: 120%;
    }
    &:hover {
      background: #fff;
      color: #2B2A29;
    }
  }
  &__img {
    position: absolute;
    z-index: 2;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    max-width: 408px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 300px;
      height: 300px;
      background: rgb(255, 255, 255);
      border-radius: 50%;
      filter: blur(250px);
      opacity: 0.2;
      pointer-events: none;
    }
    img {
      max-width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
}
.products {
  padding: 25px 0 20px;
  &__slider {
    position: relative;
    margin-top: 20px;
  }
  .swiper-main {
    padding-bottom: 24px;
    .swiper-pagination {
      top: unset;
      bottom: 0;
      height: 2px;
      &-progressbar {
        background: #E5E5E5;
        &-fill {
          background: #2B2A29;
        }
      }
    }
  }
  .swiper-button-prev {
    left: -58px;
  }
  .swiper-button-next {
    right: -58px;
  }
}
.product-card {
  border: 1px solid #E5E5E5;
  overflow: hidden;
  &__top {
    position: relative;
    padding-bottom: 100%;
  }
  &__info {
    position: absolute;
    z-index: 2;
    top: 10px;
    left: 10px;
    right: 10px;
    display: flex;
    align-items: center;
    gap: 4px;
  }
  &__discount {
    background: rgba(241, 241, 241, 0.7);
    padding: 1.5px 7.5px;
    font-size: 11px;
    line-height: 120%;
    color: #E22429;
  }
  &__new {
    background: rgba(241, 241, 241, 0.7);
    padding: 1.5px 7.5px;
    font-size: 11px;
    line-height: 120%;
  }
  &__favorite {
    position: absolute;
    top: 0;
    right: 0;
    width: 16px;
    cursor: pointer;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__slider {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .swiper {
    width: 100%;
    overflow: unset;
  }
  .swiper-slide {
    position: relative;
    display: flex;
    justify-content: center;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 150px;
      height: 150px;
      background: #F6F6F6;
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 1;
      max-width: 100%;
    }
  }
  .swiper-pagination {
    top: unset !important;
    bottom: 24px !important;
    height: 0 !important;
    &-bullet {
      width: 5px;
      height: 5px;
      background: #83878D;
      opacity: 1 !important;
    }
    &-bullet-active {
      background: #2B2A29;
    }
  }
  &__video {
    position: absolute;
    z-index: 2;
    left: 18px;
    bottom: 18px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 22px;
      height: 22px;
      background: #F6F6F6;
      border-radius: 50%;
    }
    img {
      position: relative;
      z-index: 2;
      max-width: 11px;
    }
  }
  &__desc {
    padding: 16px;
  }
  &__title {
    font-family: 'PlayfairDisplay';
    font-size: 16px;
    line-height: 120%;
    color: #83878D;
  }
  &__subtitle {
    min-height: 43px;
    font-size: 18px;
    line-height: 120%;
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 4px;
    margin-top: 18px;
    span:nth-child(1) {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
    }
    span:nth-child(2) {
      font-size: 14px;
      line-height: 120%;
      color: #83878D;
      text-decoration: line-through;
    }
  }
  &__info2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4px;
    border-top: 1px solid #E5E5E5;
    padding-top: 16px;
    margin-top: 16px;
    span {
      font-size: 18px;
      line-height: 120%;
      color: #83878D;
    }
    i {
      width: 24px;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.article-list {
  padding: 20px 0;
  &__inner {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
  }
  &__text {
    p {
      font-weight: 300;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    padding-left: 60px;
    li {
      position: relative;
      font-weight: 300;
      &:after {
        content: '';
        position: absolute;
        top: 10px;
        left: -22px;
        width: 4px;
        height: 4px;
        background: #DB161B;
        border-radius: 50%;
      }
    }
  }
  &__list2 {
    li {
      list-style: auto;
      padding-left: 5px;
      margin-left: -5px;
      &:after {
        display: none;
      }
      &::marker {
        font-family: 'PlayfairDisplay';
      }
    }
  }
}
.article-recommendations {
  padding: 20px 0;
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
    margin-top: 20px;
  }
}
.article-recommendations__card {
  display: flex;
  align-items: center;
  gap: 30px;
  border: 1px solid #E5E5E5;
  padding: 15px 45px 15px 10px;
  &:hover {
    color: #2b2a29 !important;
    text-decoration: unset !important;
  }
  &-img {
    width: 266px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &-desc {
    position: relative;
  }
  &-date {
    font-size: 12px;
    line-height: 120%;
    color: #83878D;
    text-transform: uppercase;
  }
  &-title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 12px;
  }
  &-text {
    margin-top: 12px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
    p {
      font-size: 14px;
      line-height: 140%;
      font-weight: 300;
    }
  }
  &-arrow {
    position: absolute;
    top: 50%;
    right: -32px;
    transform: translate(0, -50%);
    width: 20px;
    img {
      width: 100%;
    }
  }
}
.article-categories {
  padding: 20px 0 40px;
  &2 {
    padding-top: 5px !important;
    .article-categories__cards {
      margin-top: 0;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 25px;
    margin-top: 35px;
  }
  &__info {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 18px;
    border-bottom: 1px solid #2B2A29;
    padding-bottom: 30px;
    margin-top: 75px;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 18px;
  }
  .favorite-wrap {
    display: flex;
    align-items: center;
    gap: 6px;
  }
  .favorite,
  .send {
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F1F1F1;
    border-radius: 50%;
    cursor: pointer;
    svg,
    img {
      max-width: 22px;
      height: auto;
    }
  }
  .quote__user {
    align-items: start;
    margin-top: 20px;
    &-text {
      max-width: 640px;
      span {
        font-size: 16px;
        font-weight: 300;
        margin-top: 5px;
      }
    }
  }
}
.article-categories__card {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #E5E5E5;
  padding: 40px 26px;
  &-top {
    width: 100%;
    padding-right: 90px;
  }
  &-number {
    position: absolute;
    top: 0;
    right: 26px;
    width: 82px;
    height: 82px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #2B2A29;
    color: #fff;
    font-variant: lining-nums;
  }
  &-img {
    margin-top: 32px;
    img {
      width: 100%;
    }
  }
  &-text {
    margin-top: 20px;
    p {
      font-weight: 300;
    }
  }
  &-btn {
    font-size: 12.7px;
    padding: 14px 24px;
    margin-top: 30px;
  }
}
.articles-related {
  padding: 40px 0;
  &__top {
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }
  &__slider-btns {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
  }
  &__slider {
    position: relative;
    padding-bottom: 32px;
    margin-top: 30px;
  }
  .swiper-pagination {
    top: unset;
    bottom: 0;
    height: 2px;
    &-progressbar {
      background: #E5E5E5;
      &-fill {
        background: #2B2A29;
      }
    }
  }
}
.articles-related__card {
  &:hover {
    color: #2b2a29 !important;
    text-decoration: unset !important;
  }
  &-img {
    position: relative;
    padding-bottom: 100%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &-info {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 4px 12px;
    margin-top: 16px;
  }
  &-date {
    font-size: 12px;
    line-height: 120%;
    font-weight: 700;
    color: #83878D;
    text-transform: uppercase;
  }
  &-theme {
    font-size: 12px;
    line-height: 120%;
    font-weight: 700;
    color: #83878D;
    text-transform: uppercase;
    background: #F1F1F1;
    padding: 6px 8px;
  }
  &-group {
    position: relative;
    padding-right: 32px;
  }
  &-title {
    font-size: 18px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 10px;
  }
  &-text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:3;
    -webkit-box-orient: vertical;
    margin-top: 10px;
    p {
      font-size: 16px;
      line-height: 140%;
    }
  }
  &-arrow {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
  }
}


@media screen and (max-width: 992px) {
  .article-main {
    &__info {
      gap: 10px 25px;
      padding-right: 49px;
    }
    &__text {
      p {
        font-size: 22px;
      }
    }
    &__item {
      margin-top: 32px;
    }
  }
  .article-gallery {
    &__title {
      padding-right: 116px;
    }
    .swiper-button-prev {
      top: -68px;
      left: unset;
      right: 58px;
      transform: unset;
      margin: 0;
    }
    .swiper-button-next {
      top: -68px;
      right: 0;
      transform: unset;
      margin: 0;
    }
  }
  .article-table {
    &__title {
      padding-right: 15px;
    }
  }
  .banner {
    &__inner {
      padding: 50px 32px;
      &:after {
        width: calc(100% - 24px);
        height: calc(100% - 24px);
      }
    }
    &__desc {
      max-width: 286px;
    }
    &__text {
      p {
        font-size: 14.5px;
      }
    }
    &__btn {
      font-size: 14.5px;
    }
    &__img {
      max-width: 350px;
    }
  }
  .products {
    .swiper-button-prev {
      left: -24px;
    }
    .swiper-button-next {
      right: -24px;
    }
  }
  .product-card {
    &__Info2 {
      span {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .article-info {
    &__inner {
      padding-bottom: 40px;
    }
    &__flex {
      grid-template-columns: 100%;
      gap: 20px;
    }
  }
  .article-gallery {
    &__inner {
      padding-top: 40px;
    }
  }
  .article-recommendations__card {
    &-img {
      width: 220px;
    }
  }
}

@media screen and (max-width: 700px) {
  .banner {
    &__inner {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      gap: 20px;
      padding: 0;
    }
    &__desc {
      max-width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 0 36px 36px 36px;
    }
    &__img {
      position: relative;
      top: unset;
      right: unset;
      transform: unset;
      max-width: 100%;
      width: 100%;
      order: -1;
      img {
        width: 100%;
      }
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    &__btn {
      padding: 16px 20px;
    }
  }
}

