.certificate-main {
  padding-bottom: 25px;
  &__img {
    margin-top: 40px;
    img {
      width: 100%;
      max-width: 100%;
    }
  }
  &__text {
    max-width: 990px;
    display: grid;
    grid-template-columns: 100%;
    text-align: center;
    gap: 15px;
    margin: 75px auto 0;
    p {
      font-size: 22px;
      line-height: 130%;
      font-weight: 300;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    align-items: start;
    gap: 30px;
    margin-top: 75px;
  }
  table {
    width: 100%;
    margin-top: 30px;
    thead {
      tr {
        th {
          text-align: start;
          padding-bottom: 12px;
        }
        th:not(:last-child) {
          padding-right: 30px;
        }
      }
    }
    tbody {
      tr {
        td {
          padding-bottom: 30px;
        }
        td:not(:last-child) {
          padding-right: 30px;
        }
        td:nth-child(1) {
          width: 210px;
        }
      }
    }
  }
}
.certificate-main__card {
  min-height: 628px;
  display: flex;
  flex-direction: column;
  border: 1px solid #E5E5E5;
  &-top {
    min-height: 275px;
    max-height: 275px;
    border-bottom: 1px solid #E5E5E5;
  }
  &-img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
      min-height: 275px;
    }
  }
  &-slider {
    position: relative;
    width: 100%;
    height: 100%;
    min-height: 275px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 16px 70px;
  }
  .swiper {
    max-width: 330px;
    margin: 0 auto;
  }
  .swiper-slide {
    img {
      width: 100%;
    }
  }
  .swiper-button-prev {
    left: 15px;
  }
  .swiper-button-next {
    right: 15px;
  }
  .swiper-pagination {
    bottom: 10px;
    &-bullet {
      width: 5px;
      height: 5px;
      background: #83878D;
      opacity: 1 !important;
    }
    &-bullet-active {
      background: #2B2A29;
    }
  }
  &-desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 32px;
  }
  &-text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 32px;
    margin-bottom: 32px;
    p {
      font-size: 18px;
      line-height: 130%;
      font-weight: 300;
    }
  }
  &-btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 10px / 2));
    gap: 10px;
    margin-top: auto;
  }
  &-btn {
    span {
      font-size: 18px;
    }
  }
  &-tabs {
    display: inline-flex;
    align-items: stretch;
    border: 1px solid #F6F6F6;
    margin-top: 24px;
  }
  &-tab {
    padding: 6px 12px;
    cursor: pointer;
    span {
      font-size: 14px;
      line-height: 120%;
      font-weight: 300;
    }
  }
  .active-tab {
    background: #F5F5F5;
    span {
      font-weight: 400;
    }
  }
  &-subtitle {
    font-size: 16px;
    line-height: 130%;
    font-weight: 400;
    color: #83878D;
  }
  .nice-select {
    width: 100%;
    height: auto;
    background: transparent;
    border: none;
    border-bottom: 1px solid #83878D;
    border-radius: 0;
    padding: 0 0 10px 0;
    &:after {
      right: 6px;
      transform: rotate(0);
      background-image: url("../img/arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
    }
    .current {
      font-size: 18px;
      line-height: 120%;
      font-weight: 400;
    }
  }
  .cartcalc {
    display: flex;
    align-items: stretch;
    margin-top: 4px;
    input {
      width: 44px;
      text-align: center;
      background: transparent;
      border: none;
      font-size: 18px;
      line-height: 120%;
    }
  }
  .ccalc-minus,
  .ccalc-plus {
    width: 24px;
    height: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #F6F6F6;
    border-radius: 4px;
    cursor: pointer;
  }
  .add-wrap__btn {
    gap: 12px;
    background: #F6F6F6;
    padding: 4px 12px;
    i {
      width: 17px;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 18px;
      line-height: 120%;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  .delete-btn {
    width: 19px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &-input {
    input {
      width: 100%;
      height: 40px;
      border: none;
      border-bottom: 1px solid #83878D;
      font-size: 16px;
      line-height: 120%;
      color: #2B2A29;
      &::placeholder {
        color: rgba(131, 135, 141, 0.5);
      }
    }
  }
}
.certificate-info {
  padding: 25px 0 40px;
  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
  }
  &__subtitle {
    font-family: 'PlayfairDisplay';
    font-size: 25px;
    line-height: 120%;
    color: #626161;
    margin-top: 8px;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 30px;
    li {
      position: relative;
      display: flex;
      align-items: start;
      font-size: 18px;
      line-height: 140%;
      font-weight: 300;
      list-style: auto;
      padding-left: 50px;
      span {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        align-items: start;
        font-family: 'PlayfairDisplay';
        font-size: 45px;
        line-height: 60%;
        font-variant: lining-nums;
      }
    }
  }
  .mob-title {
    display: none;
  }
  &__tabs {
    display: none;
    align-items: stretch;
    border: 1px solid #F6F6F6;
    margin-top: 20px;
  }
  &__tab {
    padding: 8px 12px;
    cursor: pointer;
    span {
      font-size: 14px;
      line-height: 120%;
    }
  }
  .active-tab {
    background: #F5F5F5;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 30px / 2));
    gap: 30px;
    margin-top: 75px;
  }
}
.certificate-info__card {
  display: flex;
  align-items: start;
  gap: 30px;
  border: 1px solid #E5E5E5;
  padding: 32px 24px;
  &-icon {
    width: 72px;
    height: 72px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #EFEFEF;
    border-radius: 50%;
    img {
      max-width: 40px;
    }
  }
  &-text {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 15px;
    p {
      font-size: 18px;
      line-height: 140%;
      font-weight: 300;
    }
  }
}
.certificate-info2 {
  padding: 40px 0;
  &__inner {
    background-image: url("../img/certificate-info2__bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border: 1px solid #E5E5E5;
    padding: 75px 55px;
  }
  &__wrap {
    max-width: 585px;
  }
  &__title {
    font-family: 'PlayfairDisplay';
    font-size: 43px;
    line-height: 120%;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 140%;
    font-weight: 300;
    margin-top: 24px;
  }
  &__list {
    display: flex;
    gap: 16px;
    margin-top: 24px;
    li {
      max-width: 207px;
      min-width: 169px;
      display: flex;
      align-items: start;
      gap: 8px;
      font-size: 14px;
      line-height: 140%;
      i {
        width: 20px;
        flex-shrink: 0;
        img {
          max-width: 100%;
        }
      }
    }
  }
  &__btn {
    padding: 20px 30px 18px 30px;
    margin-top: 45px;
    span {
      font-size: 18px;
    }
  }
}
.certificate-info3 {
  padding: 40px 0 80px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 30px;
  }
  &__left {
    width: calc(100% - 410px);
    .certificate-info3__link {
      margin-left: 42px;
    }
  }
  &__right {
    width: 380px;
    border: 1px solid #E5E5E5;
    padding: 32px 24px;
  }
  &__title {
    font-family: 'PlayfairDisplay';
    font-size: 43px;
    line-height: 120%;
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 32px;
    margin-bottom: 32px;
    padding-left: 20px;
    li {
      position: relative;
      font-size: 18px;
      line-height: 140%;
      font-weight: 300;
      padding-left: 22px;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 5px;
        height: 5px;
        background: #DB161B;
        border-radius: 50%;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
    margin: 24px 0;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 20px;
    &-icon {
      width: 42px;
      height: 42px;
      flex-shrink: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #EFEFEF;
      border-radius: 50%;
      img {
        max-width: 24px;
      }
    }
    &-text {
      p {
        font-size: 14px;
        line-height: 140%;
        font-weight: 300;
      }
    }
  }
  &__link {
    display: inline-block;
    font-size: 16px;
    line-height: 140%;
    color: #2b2a29 !important;
    border-bottom: 1px solid #E22429;
    &:hover {
      text-decoration: unset !important;
      border-color: transparent;
    }
  }
}


@media screen and (max-width: 1200px) {
  .certificate-main__card {
    &-btns {
      grid-template-columns: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .certificate-main {
    &__img {
      margin-top: 28px;
    }
    &__text {
      margin-top: 56px;
      p {
        font-size: 18px;
      }
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      margin-top: 56px;
    }
  }
  .certificate-main__card {
    &-slider {
      padding: 16px 24px;
    }
    .swiper-button-prev {
      left: 6px;
    }
    .swiper-button-next {
      right: 6px;
    }
    &-desc {
      padding: 18px;
    }
    &-tabs {
      margin-top: 24px;
    }
    .tab__content {
      position: relative;
      padding-bottom: 32px;
      margin-bottom: 24px;
    }
    table {
      margin-top: 24px;
      thead {
        tr {
          th:not(:last-child) {
            padding-right: 16px;
          }
          th:last-child {
            display: none;
          }
        }
      }
      tbody {
        tr {
          td {
            padding-bottom: 16px;
          }
          td:not(:last-child) {
            padding-right: 16px;
          }
          td:nth-child(1) {
            width: 150px;
          }
          td:nth-child(2) {
            width: 92px;
          }
        }
      }
    }
    &-select {
      max-width: 150px;
    }
    .add-wrap {
      position: absolute;
      left: 0;
      bottom: 0;
      .certificate-main__card-subtitle {
        display: none;
      }
    }
  }
  .certificate-info {
    padding-bottom: 30px;
    &__subtitle {
      font-size: 20px;
    }
    &__list {
      gap: 12px;
      li {
        font-size: 16px;
        padding-left: 40px;
        span {
          font-size: 40px;
        }
      }
    }
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 24px / 2));
      gap: 24px;
      margin-top: 56px;
    }
  }
  .certificate-info__card {
    gap: 16px;
    &-text {
      gap: 10px;
      margin-top: 10px;
      p {
        font-size: 14px;
      }
    }
  }
  .certificate-info2 {
    padding: 30px 0;
    &__inner {
      padding: 32px;
    }
    &__wrap {
      max-width: 340px;
    }
    &__title {
      font-size: 24px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 16px;
    }
    &__list {
      flex-direction: column;
      gap: 8px;
      margin-top: 16px;
      li {
        max-width: 100%;
      }
    }
    &__btn {
      margin-top: 32px;
    }
  }
  .certificate-info3 {
    padding: 30px 0 60px;
    &__inner {
      gap: 16px;
    }
    &__left {
      width: calc(100% - 278px);
      .certificate-info3__link {
        margin-left: 32px;
      }
    }
    &__right {
      width: 262px;
      padding: 24px 16px;
    }
    &__title {
      font-size: 24px;
    }
    &__list {
      padding-left: 10px;
      margin: 16px 0;
      li {
        font-size: 15px;
        &:after {
          top: 5px;
        }
      }
    }
    &__link {
      font-size: 15px;
    }
    &__item {
      &-text {
        p {
          font-size: 14px;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .certificate-main {
    &__img {
      margin-top: 20px;
    }
    &__text {
      margin-top: 48px;
      p {
        font-size: 16px;
      }
    }
    &__cards {
      margin-top: 48px;
    }
  }
  .certificate-info {
    &__cards {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
      margin-top: 48px;
    }
  }
}

@media screen and (max-width: 576px) {
  .certificate-main {
    padding-bottom: 20px;
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .certificate-main__card {
    min-height: unset;
  }
  .certificate-info {
    padding: 20px 0;
    &__tabs {
      display: inline-flex;
    }
    &__wrap {
      grid-template-columns: 100%;
      .certificate-info__title,
      .certificate-info__subtitle {
        display: none;
      }
    }
    &__list {
      margin-top: 20px;
    }
    .mob-title {
      display: block;
    }
    .tab__content {
      display: none;
      &--active {
        display: block;
      }
    }
    &__cards {
      grid-template-columns: 100%;
    }
  }
  .certificate-info__card {
    padding: 24px 16px;
  }
  .certificate-info2 {
    padding: 20px 0;
    &__inner {
      background-image: url("../img/certificate-info2__bg2.webp");
      padding: 32px 24px 330px;
    }
  }
  .certificate-info3 {
    padding: 20px 0 40px;
    &__inner {
      flex-direction: column;
      gap: 32px;
    }
    &__left {
      width: 100%;
      .certificate-info3__link {
        margin-left: 22px;
      }
    }
    &__right {
      width: 100%;
    }
    &__list {
      padding-left: 0;
    }
  }
}

@media screen and (max-width: 400px) {
  .certificate-info2 {
    &__btn {
      width: 100%;
    }
  }
}