.h1 {
  font-family: 'PlayfairDisplay';
  font-size: 60px;
  line-height: 120%;
}
.btn-black {
  background: #2B2A29F2;
  border-radius: 0;
  padding: 19.5px;
  span {
    line-height: 105%;
    color: #fff;
    text-transform: uppercase;
  }
  &:hover {
    background: #000;
  }
}
.btn-black__outline {
  background: transparent;
  border: 1px solid #2B2A29F2;
  border-radius: 0;
  padding: 18px;
  span {
    line-height: 105%;
    color: #2B2A29F2;
    text-transform: uppercase;
  }
  &:hover {
    background: #2B2A29F2;
    span {
      color: #fff;
    }
  }
}
.text43 {
  font-family: 'PlayfairDisplay';
  font-size: 43px;
  line-height: 120%;
}
.text30 {
  font-family: 'PlayfairDisplay';
  font-size: 30px;
  line-height: 120%;
}
.text20 {
  font-size: 20px;
  line-height: 140%;
}
.modal {
  background: rgba(#000, .3);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 582px;
    max-width: 100%;
    position: relative;
    background: #ffffff;
    margin: 0 auto;
    padding: 32px 50px 50px;
    @media screen and (max-width: 992px) {
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 20px;
    right: 16px;
    width: 16px;
    background: transparent;
    padding: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    position: relative;
    font-family: 'PlayfairDisplay';
    font-size: 35px;
    line-height: 120%;
    padding-bottom: 24px;
    margin-bottom: 34px;
    &:after {
      content: '';
      position: absolute;
      left: -50px;
      bottom: 0;
      width: calc(100% + 100px);
      border-bottom: 1px solid #E5E5E5;
    }
  }
  &-form {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    &__group {
      position: relative;
      label {
        position: relative;
        font-size: 16px;
        line-height: 120%;
        font-weight: 400;
        color: #83878D;
        span {
          content: '';
          position: absolute;
          top: 7px;
          right: -12px;
          width: 4px;
          height: 4px;
          background: #E22429;
          border-radius: 50%;
        }
      }
      input {
        width: 100%;
        height: 40px;
        border: none;
        border-bottom: 1px solid #83878D;
        font-size: 16px;
        line-height: 120%;
        color: #2B2A29;
        &::placeholder {
          color: rgba(131, 135, 141, 0.5);
        }
      }
      textarea {
        width: 100%;
        height: 75px;
        border: none;
        border-bottom: 1px solid #83878D;
        padding: 8px 0;
        font-size: 16px;
        line-height: 120%;
        color: #2B2A29;
        resize: none;
        &::placeholder {
          color: rgba(131, 135, 141, 0.5);
        }
      }
    }
    &__check {
      position: relative;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding: 0;
        margin: 0;
        opacity: 0;
        cursor: pointer;
      }
      input:checked ~ label {
        &:after {
          background-image: url("../img/check.svg");
        }
      }
      label {
        position: relative;
        display: block;
        font-size: 12px;
        line-height: 120%;
        font-weight: 400;
        padding-left: 35px;
        &:after {
          content: '';
          position: absolute;
          top: -4px;
          left: 0;
          width: 18px;
          height: 18px;
          background-repeat: no-repeat;
          background-position: center;
          background-size: auto;
          border: 1px solid #2B2A29;
          border-radius: 4px;
        }
        a {
          position: relative;
          z-index: 3;
          color: #2b2a29 !important;
          border-bottom: 1px solid transparent;
          text-decoration: unset !important;
          &:hover {
            border-color: #e22429;
          }
        }
      }
    }
    &__btn {
      span {
        font-size: 18px;
      }
    }
  }
}
.fancybox-content {
  position: unset !important;
  background: transparent !important;
  padding: 0 !important;
}
.fancybox-slide {
  display: flex !important;
}
.fancybox-button {
  svg path {
    fill: #fff !important;
  }
}
.swiper-button-next,
.swiper-button-prev {
  width: 48px;
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid rgba(246, 246, 246, 1);
  border-radius: 50%;
  &:after {
    display: none;
  }
  &:hover {
    background: rgba(246, 246, 246, 1);
  }
  img {
    width: 10px;
  }
}
.favorite--active {
  svg path {
    fill: #2B2A29F2;
    stroke: #2B2A29F2;
    fill-opacity: 1;
  }
}
.input-group {
  border-bottom: 1px solid #83878D;
  input {
    width: 100%;
    height: 40px;
    background: transparent !important;
    border: none !important;
    box-shadow: unset !important;
    padding: 0;
    font-size: 16px;
    line-height: 120%;
    color: #2B2A29;
    &::placeholder {
      opacity: 0;
      color: #ff0033;
    }
    &::selection {
      background: transparent !important;
    }
  }
}
#btn-country {
  align-items: center;
  border: none;
  padding: 0;
  .flag {
    order: -2;
  }
  .caret {
    order: -1;
  }
}
.dropdown-menu {
  top: calc(100% - 5px);
  display: block;
  opacity: 0;
  pointer-events: none;
  margin-top: 0;
  &--active {
    opacity: 1;
    pointer-events: auto;
  }
}


@media screen and (max-width: 992px) {
  .h1 {
    font-size: 40px;
  }
  .text30 {
    font-size: 24px;
  }
  .modal {
    &__dialog {
      width: 400px;
      padding: 32px;
    }
    &__title {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .h1 {
    font-size: 24px;
  }
  .text43 {
    font-size: 30px;
  }
  .text20 {
    font-size: 18px;
  }
  .modal {
    &__dialog {
      width: 100%;
    }
    &__title {
      font-size: 24px;
    }
  }
}